import { Component, OnInit } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Observable} from "rxjs";
import * as $ from 'jquery';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css',   "../..//node_modules/bootstrap/dist/css/bootstrap.min.css",
]
})
export class AppComponent implements OnInit {
  title = 'Bebou';
  listObject:Array<Object>=[]
  flipDiv =[];

  constructor(private http:HttpClient) {
  }

  onClick(i) {
    console.log( this.flipDiv[i])
    this.flipDiv[i] = !this.flipDiv[i];
  }
  
  ngOnInit(){
  this.http.get<[]>("https://distancecouple.herokuapp.com/sweetWord").subscribe(res=>{
    this.listObject=res
    for(let i=0;i</*this.listObject.length*/5;i++){
      this.flipDiv[i]=false
    }
  })
  $(document).ready(function(){
    $('.flip').click(function(){
      $(this).find('.card').toggleClass('flipped');
      console.log("test")
});
});

  }
}
